<template>
  <div style="margin:0px 30px 30px 30px !important">
    <v-row class="ml-2 mr-2">
      <v-col lg="4">
        <v-text-field
          v-model="concept"
          class="customText"
          hint="Introduce la cadena a buscar"
          label="Concepto"
          :error-messages="conceptErrors"
          @input="
            $v.concept.$touch();
            filterConcept();
          "
          @blur="$v.concept.$touch()"
        ></v-text-field>
      </v-col>
      
      <v-col lg="2">
        <v-text-field
          v-model="minAmount"
          hint="Introduce un número no negativo"
          label="Importe mínimo"
          type="number"
          class="customText"
          :error-messages="minAmountErrors"
          @input="filterMinAmount()"
          @blur="$v.minAmount.$touch()"
        ></v-text-field>
      </v-col>
      <v-col lg="2">
        <v-text-field
          v-model="maxAmount"
          label="Importe máximo"
          type="number"
          hint="Introduce un número no negativo"
          class="customText"
          :error-messages="maxAmountErrors"
          @input="filterMaxAmount()"
          @blur="$v.maxAmount.$touch()"
        ></v-text-field>
      </v-col>
      <v-col lg="4">
        <v-select
          :items="combos"
          class="customText"
          v-model="category"
          label="Categoría"
          @input="filterCategory"
          multiple
        ></v-select>
      </v-col>
      </v-row>
      <v-row class="ml-2 mr-2">
      <v-col lg="4">
        <v-select
          :items="types"
          v-model="type"
          class="customText"
          label="Tipo"
          @input="filterType"
        ></v-select>
      </v-col>
      <v-col justify="center" align="center" lg="2">
        <v-menu
          v-model="menu1"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="initialDate"
              label="Fecha de inicio"
              class="customText"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="initialDate"
            @input="filterInitialDate"
            :landscape="true"
          ></v-date-picker>
        </v-menu>
      </v-col>
      <v-col justify="center" align="center" lg="2">
        <v-menu
          v-model="menu"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="finalDate"
              label="Fecha de fin"
              class="customText"
              readonly
              :error-messages="finalDateErrors"
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="finalDate"
            @input="
              $v.finalDate.$touch();
              filterFinalDate();
            "
            @blur="$v.finalDate.$touch()"
            :landscape="true"
          ></v-date-picker>
        </v-menu>
      </v-col>
      <v-col lg="2">
        <div class="text-center">
          <v-btn large elevation="2" @click="filter" color="primary"
            >Filtrar</v-btn
          >
        </div>
      </v-col>
      <v-col lg="2">
        <div class="text-center">
          <v-btn large elevation="2" @click="clear" color="primary"
            >Limpiar</v-btn
          >
        </div>
      </v-col>
    </v-row>
    <v-row align="center" justify="center">
      <v-col md="12" lg="9" xl="9">
        <!-- <Table style="border: 3px solid #13314a" :values="movs"> </Table> -->
        <HistoryTable :movs="movs" v-on:update="update"></HistoryTable>
      </v-col>
      <v-col md="12" lg="3" xl="3" style="margin-left: 0px; flex-direction: column">
        <v-card
            class="elevation-15 text-center"
            height="443px"
            style="
              border: 3px solid #13314a;
              border-radius: 20px;
              background-color: #f2fcff !important;
              padding-top: 20px;
            "
          >
        <Pie
          :movements="movs" :link="true"
        />
        </v-card>
      </v-col>
    </v-row>
    <v-btn
      fab
      dark
      large
      color="primary"
      fixed
      right
      bottom
      class="mb-14"
      @click="dialog = true"
      style="font-size:2em"
      >?</v-btn
    >

    <Help title="HISTORIAL" v-model="dialog" />
  </div>
</template>

<style scoped>
.customText >>> .v-label {
  color: #13314a;
  font-size: 1.1em;
}

.customText >>> input {
  font-size: 1.2em;
}

.customText >>> .v-icon {
  color: #13314a;
}
</style>

<script>
import Help from "../components/Help";
import Pie from "../components/Charts/Pie";
import Multiple from "../components/Charts/Multiple";
import Donut from "../components/Charts/Donut";
import HistoryTable from "../components/HistoryTable";
import { mapState, mapMutations } from "vuex";
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  minValue,
  minLength,
  email,
  alphaNum,
  decimal,
  requiredIf,
  numeric,
  integer,
} from "vuelidate/lib/validators";

export default {
  name: "History",
  components: {
    Pie,
    HistoryTable,
    Multiple,
    Donut,
    Help,
  },
  mixins: [validationMixin],
  validations: {
    concept: { maxLength: maxLength(20) },
    minAmount: {
      decimal: function(value) {
        if (value == undefined || value === "") return true;
        if (this.isNumeric(value)) return true;
        return false;
      },
      minAmount: function(value) {
        if (value == undefined || value === "") return true;
        return value >= 0.01;
      },
      twoDecimals: function(value) {
        if (value == undefined || value === "") return true;
        return (value * 100) % 1 == 0;
      },
    },
    maxAmount: {
      decimal: function(value) {
        if (value == undefined) return true;
        if (this.isNumeric(value)) return true;
        return false;
      },
      minAmount: function(value) {
        if (value == undefined || value === "") return true;
        return value >= 0.01;
      },
      twoDecimals: function(value) {
        if (value == undefined || value === "") return true;
        return (value * 100) % 1 == 0;
      },
      greater: function(value) {
        if (
          this.minAmount == null ||
          this.minAmount == undefined ||
          value === "" ||
          this.maxAmount == undefined ||
          this.maxAmount == null ||
          this.maxAmount == ""
        )
          return true;
        return value > this.minAmount;
      },
    },
    finalDate: {
      isAfter: function(date) {
        if (
          this.initialDate === "" ||
          this.initialDate === null ||
          this.finalDate === "" ||
          this.finalDate === null
        )
          return true;
        var initial = new Date(this.initialDate);
        var final = new Date(date);
        return final > initial;
      },
    },
  },
  computed: {
    conceptErrors() {
      const errors = [];
      if (!this.$v.concept.$dirty) return errors;
      !this.$v.concept.maxLength &&
        errors.push("El concepto debe ocupar como máximo 20 caracteres.");
      return errors;
    },
    minAmountErrors() {
      const errors = [];
      if (!this.$v.minAmount.$dirty) return errors;
      !this.$v.minAmount.decimal &&
        errors.push("El importe debe ser numérico.");
      !this.$v.minAmount.minAmount &&
        errors.push("El importe debe ser superior a 0€.");
      !this.$v.minAmount.twoDecimals &&
        errors.push("El importe debe tener como máximo 2 decimales.");
      return errors;
    },
    maxAmountErrors() {
      const errors = [];
      if (!this.$v.maxAmount.$dirty) return errors;
      !this.$v.maxAmount.decimal &&
        errors.push("El importe debe ser numérico.");
      !this.$v.maxAmount.minAmount &&
        errors.push("El importe debe ser superior a 0€.");
      !this.$v.maxAmount.greater &&
        errors.push(
          "El importe debe ser superior al importe mínimo seleccionado"
        );
      !this.$v.maxAmount.twoDecimals &&
        errors.push("El importe debe tener como máximo 2 decimales.");
      return errors;
    },
    finalDateErrors() {
      const errors = [];
      if (!this.$v.finalDate.$dirty) return errors;
      !this.$v.finalDate.isAfter &&
        errors.push("La fecha de fin debe ser posterior a la de inicio.");
      return errors;
    },
    ...mapState(["uniqueMovements"]),
  },
  created() {
    this.aux = [...this.uniqueMovements];
    this.movs = [...this.uniqueMovements];
  },
  data() {
    return {
      dialog: false,
      movs: [],
      aux: [],
      menu1: false,
      menu: false,
      concept: "",
      category: "",
      minAmount: undefined,
      maxAmount: undefined,
      type: "",
      initialDate: "",
      finalDate: "",
      filters: {
        concept: "",
        category: "",
        minAmount: 0,
        maxAmount: 0,
        type: "",
        initialDate: "",
        finalDate: "",
      },
      combos: ["Ocio", "Servicios", "Compras", "Ingresos", "Otro"],
      types: ["Ingreso", "Gasto"],
    };
  },
  methods: {
    update() {
      this.movs = [...this.uniqueMovements];
    },
    filter() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        console.log("error");
        console.log(this.$v.concept.$invalid);
        console.log(this.$v.minAmount.$invalid);
        console.log(this.$v.maxAmount.$invalid);
        console.log(this.$v.finalDate.$invalid);
      }
      if (!this.$v.$invalid) {
        const cf = new this.$MultiFilters(this.uniqueMovements, this.filters);
        var items = [...this.uniqueMovements];
        cf.registerFilter("concept", function(searchWord, items) {
          if (searchWord.trim() === "") return items;
          return items.filter((item) => {
            return item.concept
              .toLowerCase()
              .includes(searchWord.toLowerCase());
          }, searchWord);
        });
        cf.registerFilter("category", function(searchWords, items) {
          if (searchWords === null || searchWords.length === 0) return items;
          return items.filter((item) => {
            return searchWords.includes(item.category);
          }, searchWords);
        });
        cf.registerFilter("minAmount", function(minAmount, items) {
          if (
            minAmount === 0 ||
            minAmount === null ||
            minAmount === "" ||
            isNaN(minAmount)
          )
            return items;
          return items.filter((item) => {
            return item.amount >= minAmount;
          }, minAmount);
        });
        cf.registerFilter("maxAmount", function(maxAmount, items) {
          if (
            maxAmount === 0 ||
            maxAmount === null ||
            maxAmount === "" ||
            isNaN(maxAmount)
          )
            return items;
          return items.filter((item) => {
            return item.amount <= maxAmount;
          }, maxAmount);
        });
        cf.registerFilter("type", function(searchWord, items) {
          if (searchWord.trim() === "") return items;
          return items.filter((item) => {
            return item.type.toLowerCase().includes(searchWord.toLowerCase());
          }, searchWord);
        });
        cf.registerFilter("initialDate", function(initialDate, items) {
          if (initialDate === "" || initialDate === null) return items;
          var initialDateForm = new Date(initialDate);
          return items.filter((item) => {
            let itemDate = new Date(item.date);
            return itemDate >= initialDateForm;
          }, initialDate);
        });
        cf.registerFilter("finalDate", function(finalDate, items) {
          if (finalDate === "" || finalDate === null) return items;
          var finalDateForm = new Date(finalDate);
          return items.filter((item) => {
            let itemDate = new Date(item.date);
            return itemDate <= finalDateForm;
          }, finalDate);
        });
        this.movs = cf.runFilters();
      }
    },
    filterConcept() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        concept: this.concept,
      });
    },
    filterCategory() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        category: this.category,
      });
    },
    filterMinAmount() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        minAmount: parseFloat(this.minAmount),
      });
    },
    filterMaxAmount() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        maxAmount: parseFloat(this.maxAmount),
      });
    },
    filterType() {
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        type: this.type,
      });
    },
    filterInitialDate() {
      this.menu1 = false;
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        initialDate: this.initialDate,
      });
    },
    filterFinalDate() {
      this.menu = false;
      this.filters = this.$MultiFilters.updateFilters(this.filters, {
        finalDate: this.finalDate,
      });
    },
    clear() {
      this.$v.$reset();
      this.minAmount = undefined;
      this.maxAmount = undefined;
      this.concept = "";
      this.category = "";
      this.type = "";
      this.initialDate = "";
      this.finalDate = "";
      this.movs = [...this.uniqueMovements];
      this.filters = {
        concept: "",
        category: "",
        minAmount: 0,
        maxAmount: 0,
        type: "",
        initialDate: "",
        finalDate: "",
      };
    },
    isNumeric(str) {
      if (str === undefined || str === "") return true;
      return (
        !isNaN(str) && !isNaN(parseFloat(str)) // use type coercion to parse the _entirety_ of the string (`parseFloat` alone does not do this)...
      ); // ...and ensure strings of whitespace fail
    },
  },
};
</script>
